// Tabs
//

/* stylelint-disable selector-no-qualifying-type  */

.tabs {
  z-index: zindex("tabs");
  margin: 0;
  list-style-type: none;

  li {
    display: inline-block;
    padding: 1rem 1.5rem;
    color: color("white");
    cursor: pointer;
    border: 1px solid transparent;
    border-bottom: 0;
  }

  li.active {
    color: $font-color;
    background: $body-background;
  }

  &.tabs-light li,
  &.tabs-light li.active {
    color: inherit;
  }
  &.tabs-light li.active {
    background: color("white");
  }
}
